import React from 'react'
import { Link } from 'gatsby'
import './PostLink.css'

const PostLink = ({
  post: {
    frontmatter: { date, title, path },
  },
}) => (
  <div>
    <Link className="postLink" to={path}>
      <div>
        <h3>{title}</h3>
        <p>{date}</p>
      </div>
      <div className="arrow">→</div>
    </Link>
  </div>
)

export default PostLink
